.w100 {
    width: 100%;
}
.df {
    display: flex;
}

.dfaic {
    align-items: center;
}

.fdc {
    flex-direction: column;
}

.jcc {
    justify-content: center;
}

.jcsb {
    justify-content: space-between;
}

.screen {
    border: 1px solid;
    padding: 15px;
}

.screen + .screen {
    border-left: none;
}

.users-block_items .user-item {
    padding: 10px;
    cursor: pointer;
    border-radius: 20px;
}

.users-block_items .user-item.active {
    background-color: rgb(128 128 128 / 20%);
}

.user-item-features {
    width: 100%;
}

.users-block_items .user-item:hover,
.repo-data:hover {
    background-color: rgb(128 128 128 / 20%);
}

.user-block_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.user-features-block {
    margin-bottom: 16px;
}

.user-features {
   padding: 16px;
}

.user-features span {
    display: block;
}

.user-bio {
    padding: 10px;
    background-color: rgba(0, 0, 255, 0.2);
    margin-bottom: 16px;
    font-style: italic;
    font-weight: 500;
    border-radius: 10px;
}

.user-avatar {
    width: 100%;
    border-radius: 50%;
}

.user-item.active .user-features-block .user-avatar-block {
    display: none;
}

.user-item .user-avatar {
    width: 50px;
}

.repo-data {
    padding: 10px;
    border-radius: 20px;
}

.repo-data span {
    display: block;
}
